<template>
  <div>
    <v-navigation-drawer
      fixed
      v-model="drawer"
      app
      temporary
    >
      <v-list dense>
        <v-list-tile @click="navigateToHome">
          <v-list-tile-action>
            <v-icon>home</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Home</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile @click="navigateToChallenge">
          <v-list-tile-action>
            <v-icon>contact_mail</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Challenge</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

    <v-toolbar color="cyan darken-3" dark fixed app>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title @click="navigateToChallenge" :style="{ cursor: 'pointer'}">TTÜ Coding challenges</v-toolbar-title>
    </v-toolbar>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        drawer: null
      }
    },
    methods: {
      navigateToHome(){
        this.$router.push('/');
      },
      navigateToChallenge(){
        this.$router.push('/')
      }
    }

  }
</script>

<style></style>



