import Vue from 'vue'
import Router from 'vue-router'
import LandingPage from '@/components/LandingPage'
import Challenge from '@/components/Challenge'
import ChallengeSelector from '@/components/ChallengeSelector'


Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'LandingPage',
      component: LandingPage
    },
    {
      path: '/challenge',
      name: 'Challenge',
      component: Challenge
    },
    {
      path: '/string',
      name: 'String',
      component: ChallengeSelector
    },
    {
      path: '/array',
      name: 'Array',
      component: ChallengeSelector
    },
    {
      path: '/recursion',
      name: 'Recursion',
      component: ChallengeSelector
    },
    {
      path: '/logic',
      name: 'Logic',
      component: ChallengeSelector
    },
    {
      path: '/functional',
      name: 'Functional',
      component: ChallengeSelector
    },
    {
      path: '/algorithms',
      name: 'Algorithms',
      component: ChallengeSelector
    }
  ]
})



