<template>
  <div>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex xs12 v-if="showChallengeList">
          <v-alert outline color="info" icon="info" :value="true">
            Choose programming language and challenge
          </v-alert>
          <language-selector @langWasReset="lang = $event"></language-selector>
        </v-flex>

        <v-flex xs4 md v-if="showChallengeList">
          <v-list two-line>
            <template v-for="(item, index) in items">
              <v-divider :key="index"></v-divider>
              <v-list-tile
                @click="showDetails = !showDetails; chooseWhatToSendToPropAccordingToClickedName(item.challengeName)">
                <v-list-tile-avatar><v-icon>code</v-icon></v-list-tile-avatar>
                <v-list-tile-content @click="showChallengeList = !showChallengeList">
                  <v-list-tile-title v-html="item.challengeName"></v-list-tile-title>
                  <v-list-tile-sub-title v-html="item.challengeDifficulty"></v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
            </template>
          </v-list>
        </v-flex>
      </v-layout>
    </v-container>
    <challenge v-if="showDetails" :items="itemDependingOnChallenge"></challenge>
  </div>
</template>

<script>
  import {AXIOS} from '../networking/http-common'
  import Challenge from './Challenge.vue'
  import LanguageSelector from './LanguageSelector.vue'

  export default {
    name: "StringChallenge",
    components: {
      'challenge': Challenge,
      'language-selector': LanguageSelector
    },
    data() {
      return {
        lang: 'Java',
        showChallengeList: true,
        showDetails: false,
        items: [],
        itemDependingOnChallenge: []
      }
    },
    methods: {
      chooseWhatToSendToPropAccordingToClickedName(name) {
        for (let i = 0; i < this.items.length; i++) {
          if (this.items[i].challengeName === name) {
            this.itemDependingOnChallenge = this.items[i]
          }
        }
      }
    },
    created: function () {
      AXIOS.get('/challenges' + this.$route.path + "/" + this.lang.toLowerCase())
        .then(response => {
          this.items = response.data
        })
        .catch(e => {
          this.errors.push(e)
        });
      this.$emit('clicked', this.response)
    },
    watch: {
      lang: function () {
        AXIOS.get('/challenges/' + this.$route.path + "/" + this.lang.toLowerCase())
          .then(response => {
            this.items = response.data
          })
          .catch(e => {
            this.errors.push(e)
          });
        this.$emit('clicked', this.response)
      }
    }
  }
</script>


<style scoped></style>



