<template>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex xs12>
          <v-alert outline color="info" icon="info" :value="true">
            Choose challenge category you want to practice
          </v-alert>
        </v-flex>
        <v-flex xs6 md v-if="show">
          <v-list two-line>
            <template v-for="(item, index) in leftColumn">
              <v-divider v-if="item.divider" :inset="item.inset" :key="index"></v-divider>
              <v-list-tile v-else :key="item.title" @click="goToAccordingUri(item.title)">
                <v-list-tile-content @click="show = !show">
                  <v-list-tile-title v-html="item.title"></v-list-tile-title>
                  <v-list-tile-sub-title v-html="item.subtitle"></v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
            </template>
          </v-list>
        </v-flex>
        <v-flex xs6 md v-if="show">
          <v-list two-line>
            <template v-for="(item, index) in rightColumn">
              <v-divider v-if="item.divider" :inset="item.inset" :key="index"></v-divider>
              <v-list-tile avatar v-else :key="item.title" @click="goToAccordingUri(item.title)">
                <v-list-tile-content @click="show = !show">
                  <v-list-tile-title v-html="item.title"></v-list-tile-title>
                  <v-list-tile-sub-title v-html="item.subtitle"></v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
            </template>
          </v-list>
        </v-flex>
      </v-layout>
    </v-container>
</template>
<script>
  export default {
    name: "LandingPage",
    data() {
      return {
        show: true,
        leftColumn: [
          {
            title: "String",
            subtitle: "String manipulation with loops"
          },
          {divider: true, inset: false},
          {
            title: "Array",
            subtitle: "Array manipulation with loops"
          },
          {divider: true, inset: false},
          {
            title: "Algorithms",
            subtitle: "Usage of algorithms"
          }
        ],
        rightColumn: [
          {
            title: "Functional",
            subtitle: "Lambda expressions"
          },
          {divider: true, inset: false},
          {
            title: "Logic",
            subtitle: "Boolean logic puzzles"
          },
          {divider: true, inset: false},
          {
            title: "Recursion",
            subtitle: "Problems with functions that call themselves"
          }
        ]
      }
    },
    methods: {
      goToAccordingUri(title){
        let uri = "/" + title.toLowerCase();
        this.$router.push(uri);
      }

    },
    created: function () {

    }
  }
</script>

<style scoped></style>



